import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-tenants',
  templateUrl: './tenants.component.html',
  styleUrls: ['./tenants.component.scss']
})
export class TenantsComponent implements OnInit {

  tenantsForm: FormGroup;
  showProperyForm = false;

  constructor(private formBuilder: FormBuilder) {

  }

  ngOnInit() {
    this.tenantsForm = this.formBuilder.group({
      firstName: [null, [Validators.required]],
      otherNames: [null, [Validators.required]],
      email: [null, [Validators.required, Validators.email]],
      gender: [null, [Validators.required]],
      password: [null, [Validators.required, Validators.minLength(6)]],
      confirmPassword: [null, [Validators.required]],
      isCurrentlyRenting: ['', [Validators.required, Validators.minLength(4)]],

      // Property nested form
      property: this.formBuilder.group({
        description: [null, [Validators.required]],
        roomType: [null, [Validators.required]],
        vacationDate: [null, [Validators.required]],
        region: [null, [Validators.required]],
        town: [null, [Validators.required]],
        neighbourhood: [null, [Validators.required]],
      })
    });

    this.isCurrentlyRenting.valueChanges.subscribe(value =>
      this.showProperyForm = Number(value) ? true : false);
  }

  get isCurrentlyRenting() {
    return this.tenantsForm.controls.isCurrentlyRenting;
  }
}
